<template>
  <div class="form__counter">
    <button
      type="button"
      aria-label="  "
      @click="updateAmount(currentAmount-1)"
    >&darr;</button>

    <input type="text" v-model.number="currentAmount">

    <button
      type="button"
      aria-label="  "
      @click="updateAmount(currentAmount+1)"
    >&uarr;</button>
  </div>
</template>

<script>

  export default {
    props:['amount'],//=':amount'  .
    methods: {
      updateAmount(value){
        if(value>=0){
          this.$emit(`update:amount`, value);
        }
      },
    },
    computed: {
      currentAmount: {//currentAmount   amount       (+.sync )
        get(){
          return this.amount;//    .
        },
        set(value){// .   amount,    ,  .
          this.$emit('update:amount', value);//   (+ '.sync'  (:likes.sync='headerLikes'),  v-on:update:likes='headerLikes').
        }
      }
    },

  };
</script>

<style>
  button:hover {
    cursor: pointer;
  }
</style>
