<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <BreadCrumbItem :to="{name:'main'}" text=""/>
        <BreadCrumbItem :to="{name:'cart'}" text=""/>
        <BreadCrumbItem :to="{name:'orderInfo'}" text=" "/>
      </ul>

      <h1 class="content__title">
          <span v-if="orderInfo"> {{orderInfo.basket.id}}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary" v-if="orderInfo">
            <DictionaryItem left="" :right="orderInfo.name"/>
            <DictionaryItem left=" " :right="orderInfo.address"/>
            <DictionaryItem left="" :right="orderInfo.phone"/>
            <DictionaryItem left="Email" :right="orderInfo.email"/>
            <DictionaryItem left=" " right="  "/>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders" v-if="orderInfo">
            <li class="cart__order" v-for="item in orderInfo.basket.items">
              <h3>{{item.product.title}}</h3>
              <b>{{item.price * item.quantity}} </b>
              <span>: {{item.id}}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p v-if="orderInfo">: <b>{{orderInfo.basket.items.length}}</b>    <b>{{orderInfo.totalPrice}} </b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import {mapActions, mapGetters} from "vuex";
  import DictionaryItem from "../components/common/DictionaryItem";
  import BreadCrumbItem from "../components/common/BreadCrumbItem";

  export default {
    components: {DictionaryItem, BreadCrumbItem},
    computed: {
      ...mapGetters(['orderInfo']),
    },
    watch: {//     .       .
      '$route.params.id': {
        handler(){//  " "
          if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id){
            return;
          }
          this.$store.dispatch('loadOrderInfo', this.$route.params.id)
        },
        immediate:true,//=  created()      (  ).     created() .
      }
    },
  };

</script>

