<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a href="#" aria-label=" " class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === 1}" @click.prevent="paginate(page-1)">
<!--        <svg width="8" height="14" fill="currentColor">-->
<!--          <use xlink:href="#icon-arrow-left"></use>-->
<!--        </svg>-->
        &laquo;
      </a>
    </li>
    <li class="pagination__item" v-for="item in pages" :key="item">
      <a href="#" class="pagination__link" :class="{'pagination__link--current': item === page}" @click.prevent="paginate(item)">
        {{item}}
      </a>
    </li>
    <li class="pagination__item">
      <a href="#" aria-label=" " class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === pages}" @click.prevent="paginate(page+1)">
<!--        <svg width="8" height="14" fill="currentColor">-->
<!--          <use xlink:href="#icon-arrow-right"></use>-->
<!--        </svg>-->
        &raquo;
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'totalItems', 'perPage'], // .     camelCase  /   kebab-case. Vue    camel.  vue  html     camel.
  computed: {
    pages() {//- . .   v-for,  .
      return Math.ceil(this.totalItems / this.perPage);//v-for="item in pages" -         .
    },
  },
  methods: {
    paginate(toPage) {
      if(toPage!==0 && toPage!==(Math.ceil(this.totalItems / this.perPage)+1)){
        console.log('paginating... this.page:',this.page, 'toPage:',toPage);
        this.$emit('paginate', toPage);//   'paginate'   2       .
      }else{
        console.log('paginating ignored');
      }
    },
  },
};
</script>
