<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        152 
      </span>
    </div>
    <div class="content__catalog">
      <ProductFilter v-bind.sync="filter"/><!--        + sync-->
      <section class="catalog">
        <div style="margin: 0 auto">
          <Loader v-if="productsLoading"/>
          <div v-if="productsLoadingFailed">
            Network error
            <button @click.prevent="loadProducts">try again</button>
          </div>
        </div>
        <ProductList :products="products"/>
        <BasePagination v-model="page" :total-items="countProducts" :per-page="productsPerPage"/>
      </section>
    </div>
  </main>
</template>

<script>

  import ProductList from "../components/product/ProductList.vue";
  import Header from './../components/Header.vue';
  import Footer from './../components/Footer.vue';
  import BasePagination from '../components/common/BasePagination.vue';
  import ProductFilter from "../components/product/ProductFilter";
  import axios from "axios";
  import {API_BASE_URL} from "../config";
  import Loader from "../components/common/Loader";
  import {mapActions} from "vuex";

  export default {
    components: {
      ProductList,
      Header,
      BasePagination,
      Footer,
      ProductFilter,
      Loader,
    },
    data() {// 
      return {
        page: 1,
        productsPerPage: 4,
        filter: {
          priceFrom: 0,
          priceTo: 0,
          categoryId: 0,
          colorId: null,
        },
        productsData: null,
        productsLoading: false,
        productsLoadingFailed: false,
      };
    },
    computed: {//     ,      .
      products() {
        return this.productsData//          .       .
          ?this.productsData.items.map(item =>{//      . (      ).
            return {
              ...item,
              image:item.image.file.url
            }
          })
          :[];
        // const offset = (this.page - 1) * this.productsPerPage;//    
        // return this.filteredProducts.slice(offset, offset + this.productsPerPage);//      
      },
      countProducts() {
        return this.productsData ? this.productsData.pagination.total : 0;//          .
      },
    },
    methods: {
      ...mapActions(['loadProductsAction', 'loadCart']),//                 .
      loadProducts(){//      .
        this.productsLoading=true;
        this.productsLoadingFailed=false;
        clearTimeout(this.loadProductsTimer);//.    3 (3     = 3 )   1 - .      .     .
        this.loadProductsTimer = setTimeout(()=>{// .    - ( )(     , )
          this.loadProductsAction({
            categoryId: this.filter.categoryId,
            colorId: this.filter.colorId,
            page: this.page,
            limit: this.productsPerPage,
            minPrice: this.filter.priceFrom,
            maxPrice: this.filter.priceTo,
          })
          .then(res => this.productsData = res.data)//     .
          .catch(()=>this.productsLoadingFailed=true)
          .then(()=>this.productsLoading=false);
        },0)
      },
    },
    watch: {//     -> 
      page() {//  .    ,   .    .
        this.loadProducts();
      },
      filter: {
        handler: function () {
          this.loadProducts()
        },
        deep: true
      },
    },
    created(){// componentDidMount.    Main    ' '
      this.loadProducts();
    }
  };
</script>
