<script>
  import gotoPage from "@/helpers/gotoPage";
  import numberFormat from "@/helpers/numberFormat";
  import Counter from "../components/common/Counter";
  import ColorList from "../components/common/ColorList";
  import Loader from "../components/common/Loader";
  import {mapActions} from "vuex";
  import BreadCrumbItem from "../components/common/BreadCrumbItem";

  export default {
    components: {BreadCrumbItem, Counter, ColorList, Loader},
    // props:['pageParams'],//=':page-params'  .
    data(){
      return {
        productAmount: 1,
        productData:null,
        productLoading:false,
        productLoadingFailed:false,
        currentColor:undefined,
        isProductAdded:false,
        isProductAdding:false,
        isProductAddingFailed:false,
      }
    },
    filters: {// html -> {{product.price | numberFormat}}  -       .  Vue.   computed.
      numberFormat,
    },
    computed: {//    (=   )
      product() {
        return this.productData;
      },
      category() {
        return this.productData.category;
      },
    },
    methods: {
      ...mapActions(['addProductToCart', 'loadProductAction']),//                 .
      gotoPage,
      addToCart() {
        this.isProductAdded=false;
        this.isProductAdding=true;
        this.addProductToCart({productId: this.product.id, amount: this.productAmount})//...mapActions(['addProductToCart']) = dispatch action
        .then(()=> this.isProductAdded = true)
        .catch(() => this.isProductAddingFailed = true)
        .then(()=>this.isProductAdding = false)

      },
      loadProduct() {//  
        this.productLoading = true;
        this.productLoadingFailed = false;
        this.loadProductAction({productId: this.$route.params.id})//              id  .
          .then(res => this.productData = res.data)
          .catch(() => this.productLoadingFailed = true)
          .then(() => this.productLoading = false);
      },
    },
    watch: {//     .       .
      '$route.params.id': {
        handler(){//  " "
          this.loadProduct();
        },
        immediate:true,//=  created()      (  ).     created() .
      }
    },
  };
</script>
