<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <BreadCrumbItem :to="{name:'main'}" text=""/>
        <BreadCrumbItem :to="{name:'cart'}" text=""/>
        <BreadCrumbItem :to="{name:'order'}" text=" "/>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
         : {{cartDetailProducts.length}} .
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <!--  -->
            <BaseFormText title="" placeholder="   " v-model="formData.name" :error="formError.name"/>
            <BaseFormText title=" " placeholder="  " v-model="formData.address" :error="formError.address"/>
            <BaseFormText title="" placeholder="  " v-model="formData.phone" :error="formError.phone"/>
            <BaseFormText title="Email" placeholder="  email" v-model="formData.email" :error="formError.email"/>
            <BaseFormTextarea title="  " placeholder=" " v-model="formData.comment" :error="formError.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <OptionsItem type="radio" name="delivery"> <b></b></OptionsItem>
              <OptionsItem type="radio" name="delivery"> <b>500 </b></OptionsItem>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <OptionsItem type="radio" name="pay">  </OptionsItem>
              <OptionsItem type="radio" name="pay">  </OptionsItem>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in cartDetailProducts">
              <h3>{{item.product.title}}</h3>
              <b>{{item.product.price * item.amount}} </b>
              <span>: {{item.productId}}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>: <b>{{cartDetailProducts.length}}</b>    <b>{{cartTotalPrice}} </b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            <Loader v-show="isOrdering"/>
            <span v-show="!isOrdering"> </span>
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "../components/common/BaseFormText";
import BaseFormTextarea from "../components/common/BaseFormTextarea";
import {mapGetters} from "vuex";
import Loader from "../components/common/Loader";
import BreadCrumbItem from "../components/common/BreadCrumbItem";
import OptionsItem from "../components/common/OptionsItem";

  export default {
    components: {OptionsItem, BreadCrumbItem, BaseFormTextarea, BaseFormText, Loader},
    data(){
      return {
        formData: {},//.       v-model,             v-model
        formError:{},//  -  
        formErrorMessage: '',
        isOrdering: false,
        isOrderingFailed: false,
      }
    },
    methods: {
      order(){
        this.formError = {};
        this.formErrorMessage = '';
        this.isOrdering = true;
        this.$store.dispatch('makeOrder', this.formData)
          .catch((error)=>{
            this.formError = error.response.data.error.request || {};//            .
            this.formErrorMessage = error.response.data.error.message;
            this.isOrderingFailed = true;
          })
          .then(() => this.isOrdering = false);
      }
    },
    computed: {
      ...mapGetters(['cartDetailProducts', 'cartTotalPrice']),//        .
    }
  };

</script>

