import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import {API_BASE_URL} from "../config";
import router from "../router/index"

Vue.use(Vuex);

export default new Vuex.Store({
  state: {// .
    cart:[],//  
    cartData:[],
    isCartLoading:false,
    isCartLoadingFailed:false,
    userAccessKey: null,
    isDeleting:false,
    isDeletingFailed:false,
    orderInfo: null
  },
  mutations:{// /        React.  .  ,   .  .
    resetCart(state){
      state.cart = [];
      state.cartData = [];
    },
    updateCartProductAmount(state, payload){
      const existingItems = state.cart.find(item=>item.productId === payload.productId);
      if (existingItems){
        existingItems.amount = payload.amount;
      }
    },
    deleteCartProduct(state, productId){
      state.cart = state.cart.filter(item=>item.productId !== productId);
    },
    updateOrderInfo(state, orderInfo){
      state.orderInfo = orderInfo;
    },
    updateUserAccessKey(state, accessKey){
      state.userAccessKey = accessKey;
    },
    updateCartData(state, items){
      state.cartData = items;
    },
    updateIsCartLoading(state, bool){
      state.isCartLoading = bool;
    },
    updateIsCartLoadingFailed(state, bool){
      state.isCartLoadingFailed = bool;
    },
    syncCartProducts(state){//cart   cartData     .     1 .
      state.cart = state.cartData.map(item=>{
        return {
          productId: item.product.id,//      .
          amount: item.quantity,//      .
        }
      })
    }
  },
  getters: {
    cartDetailProducts(state) {
      return state.cart.map(item => {
        const product = state.cartData.find(p=>p.product.id===item.productId).product;//         
        return {
          ...item,
          product: {
            ...product,
            image:product.image.file.url
          },
        }
      })
    },
    cartTotalPrice(state,getters){
      return getters.cartDetailProducts.reduce((acc,item)=>(item.product.price * item.amount)+ acc, 0);
    },
    orderInfo(state){
      return state.orderInfo;
    },
  },
  actions: {// actions      .     App.
    makeOrder(context, payload){//2       payload.
      return axios
        .post(`${API_BASE_URL}/api/orders`, {
          name: payload.name,
          address: payload.address,
          phone: payload.phone,
          email: payload.email,
          comment: payload.comment
        },{
          params:{
            userAccessKey: context.state.userAccessKey
          }
        })
        .then((res)=>{
          context.commit('resetCart');
          context.commit('updateOrderInfo', res.data);
          router.push({name:'orderInfo', params: {id: res.data.id}});//      id
        })
    },
    loadOrderInfo(context,orderId){
    return axios//   axios,  .
    .get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey
        },
      })
      .then((res)=>{
        context.commit('updateOrderInfo', res.data)
      })
      .catch((err)=>{
        console.log('error from actions loadOrderInfo:',err);
        router.push({name:'notFound'});
      })
    },
    loadCart(context){//           'new Vuex.Store'  context.commit.
      context.commit('updateIsCartLoading', true);
      return axios//   axios,  .
        .get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey
          },
        })
        .then(res => {
          if(!context.state.userAccessKey){//      ...
            localStorage.setItem('userAccessKey', res.data.user.accessKey);//      
            context.commit('updateUserAccessKey',res.data.user.accessKey);//.       .         context.     ,  -  localStorage.
          }
          context.commit('updateCartData', res.data.items);//  ...
          context.commit('syncCartProducts');//... ,    ,      .
        })
        .catch(()=>{
          context.commit('updateIsCartLoadingFailed', true);
        })
        .then(()=>context.commit('updateIsCartLoading', false))
    },
    addProductToCart(context, {productId,amount}){//2       payload.
      return (new Promise(resolve => setTimeout(resolve,2000)))//   action    (   ,  )
        .then(()=>{
          return axios//   axios           <ProductPage>.
            .post(`${API_BASE_URL}/api/baskets/products`, {
              productId: productId,//    
              quantity : amount,//    
            },{
              params:{
                userAccessKey: context.state.userAccessKey
              }
            })
            .then(res => {//     
              context.commit('updateCartData', res.data.items);//   ...
              context.commit('syncCartProducts');//... ,    ,      .
            })
        })
    },
    changeProductAmountAction(context, {productId,amount}){
      console.log('changeProductAmountAction')
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          productId: productId,
          quantity : amount,
        },{
          params:{
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(res => {
          context.commit('updateCartData', res.data.items);//   ...
          context.commit('syncCartProducts');//... ,    ,      .
        })
    },
    deleteProductFromCartAction(context, {productId}){//2       payload.
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          data:{
            productId: productId,
          },
          params:{
            userAccessKey: context.state.userAccessKey
          },
        })
        .then(res => {
          context.commit('updateCartData', res.data.items);//   ...
          context.commit('syncCartProducts');//... ,    ,      .
        })
    },
    updateCartProductAmountAction(context, {productId,amount}){//  addProductToCart
      context.commit('updateCartProductAmount', {productId,amount});//           .  .
      if(amount<1){//     
        return;
      }
      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          productId: productId,
          quantity : amount,
        },{
          params:{
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(res => {
          context.commit('updateCartData', res.data.items);
        })
        .catch(()=>{//         (  )
          context.commit('syncCartProducts');
        })

    },
    loadProductsAction(context, payload){
      return axios.get(`${API_BASE_URL}/api/products`, {
        params:  {//   .
          categoryId: payload.categoryId,
          colorId: payload.colorId,
          page: payload.page,
          limit: payload.limit,
          minPrice: payload.minPrice,
          maxPrice: payload.maxPrice,
        }
      })
    },
    loadProductAction(context, {productId}) {//  
      return axios.get(`${API_BASE_URL}/api/products/${productId}`)
    },
  },
});

